
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
components: {
Group,
Cell,
Icon,
InlineXNumber,
FlexboxItem
},
props: ['dataItem'],
data () {
return {}
},
computed: {
fullName () {
return this.dataItem.gName + this.dataItem.gSpec
},
price () {
return '￥' + this.dataItem.gPrice
}
},
watch: {
'dataItem.gCount' (newVal, oldVal) {
this.changeCount(this.dataItem.gId, newVal)
}
},
methods: {
removeGoods () {
this.$emit('remove', this.dataItem.gId)
},
changeCount (gId, newVal) {
if (gId && newVal) {
this.$emit('changeCount', gId, newVal)
}
}
}
}



